.toggle-dark {
  color: rgba(0, 0, 0, 0.87);
}

.toggle-light {
  color: #fff;
}

.toggle-twirl {
  animation: twirl 250ms ease-out forwards;
}

@keyframes twirl {
  0% {
    transform: rotateY(0);
  }
  50% {
    transform: rotateY(90deg);
  }
  100% {
    transform: rotateY(180);
  }
}
